#mobile-root .freelog-pay {
    .am-modal-content {
        text-align: left !important;
        border-radius: 10px 10px 0px 0px;
    }

    .amount {
        font-size: 50px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #222222;
        line-height: 56px;

        .type {
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #666666;
            line-height: 20px;
        }
    }

    .title-item {
        padding: 20px 0 5px 0;
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #222222;
        line-height: 20px;
    }

    .content-item {
        font-size: 16px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #222222;
        line-height: 22px;

        .balance {
            font-size: 16px;
            font-family: PingFangSC-Semibold, PingFang SC;
            font-weight: 600;
            color: #999999;
            line-height: 22px;
        }
    }

    .forgot-p {
        font-size: 12px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #2784ff;
        line-height: 17px;
    }

    input {
        font-size: 14px !important;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400 !important;
    }
}

#mobile-root .pay-tip {
    .am-modal-content {
        padding: 0 !important;
    }

    .am-modal-body {
        padding: 0 30px 0 30px !important;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .am-modal-header {
        padding: 0 !important;
    }

    .paying {
        .loading {
            border: none !important;
            font-size: 20px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #2784FF;

            &::before {
                border: none !important;
            }
        }

        .success {
            i {
                color: #44C28C;
                font-size: 24px;
            }

            font-size: 16px;
            font-family: PingFangSC-Regular,
            PingFang SC;
            font-weight: 400;
            color: #222222;
        }

        .proccessing {
            font-size: 20px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #222222;
        }
    }
}

#mobile-root .input-password {
    .am-modal-content {
        padding: 0 !important;
    }

    .am-modal-body {
        padding: 0 30px 0 30px !important;
    }

    .am-modal-header {
        padding: 0 !important;
    }

    .am-modal-title {
        font-size: 16px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #222222;
        padding: 30px 0 40px 0;
    }

    input {
        width: 40px;
        height: 48px;
        caret-color: transparent;
        overflow: hidden;
        box-sizing: border-box;
        line-height: 48px;
        padding: 0px 0 0 10px !important;
        font-size: 42px;
        background: #FFFFFF;
        border-radius: 4px;
        border: 1px solid #D4D4D4;

        &:focus {
            border: 1px solid #2784FF !important;
        }
    }

    .password-forget {
        padding-top: 30px;
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #999999;
    }
}